
$primary-bg: #fff;
$primary-box: #f6f6f6;
$small-text: #696969;
* {
    padding: 0;
    margin: 0; }

.slide-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    padding: 0 5em;
    margin-bottom: 5em;
    width: 100%;
    height: 100vh;
    @media screen and (max-width:920px) {
        padding: 0 2em; }
    .header-content {
        h1 {
            text-align: center;
            margin-top: 0.5em;
            font-size: 45px;
            font-family: Garamond;

            // font: var(--unnamed-font-style-normal), normal,
            //     var(--unnamed-font-weight-bold), var(--unnamed-font-size-22), /
            //     var(--unnamed-line-spacing-38),
            //     var(--unnamed-font-family-garamond);
 }            // font: normal normal bold 35px/40px Garamond
        p {
            font-size: 18px;
            text-align: center;
            margin: 1em;
            color: $small-text;
            @media screen and (max-width:1120px) {
                padding: 0 1em; } } }

    .slick-slider {
        display: flex;
        width: 100%;
        margin: 2em 0;
        align-items: center;
        justify-content: space-evenly;
        @media screen and (max-width:920px) {
            margin: 0;
            padding: 0;
            justify-content: center;
            align-items: center; }

        .slick-list {
            margin: 0 2em;
            .slick-dots {
                display: none; }
            .main-slide {
                .slide-content {
                    background: $primary-box;
                    margin: 1em;

                    p {
                        text-align: center;
                        padding: 1em 0;
                        color: $small-text;
                        text-transform: uppercase; }

                    .slide-image {
                        width: 100%;
                        height: 250px;
                        @media screen and (max-width:1024px) {
                            height: auto; }
                        @media screen and (max-width:1024px) and (min-width:1300px) {
                            height: auto; } }

                    .slide-line {
                        width: 100%; } } } }

        .right-arrow {
            font-size: 3em;
            color: #000;
            cursor: pointer;
            user-select: none;
            z-index: 10; }

        .left-arrow {
            font-size: 3em;
            color: #000;
            cursor: pointer;
            user-select: none;
            z-index: 10; } } }
